.home_image {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  max-width: 1500px;
  flex-direction: column;
}

.home_row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}

.home-body {
  margin: 0 100px;
}
