.login_logo {
  display: flex;

  margin-top: 30px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: -20px;
  width: 150px;
}

.login_container {
  width: 300px;
  height: fit-content;
  display: flex;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}

.login_container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.login_container > form > h5 {
  margin-bottom: 5px;
}

.login_container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}

.login_container > p {
  margin-top: 15px;
  font-size: 12px;
}

.login_signinbtn {
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  cursor: pointer;
}

.login_signupbtn {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
  cursor: pointer;
}

.login_signuptext {
  margin-top: 10px;
  font-size: smaller;
  color: rgb(31, 30, 30);
}
